@import url("https://fonts.googleapis.com/css2?family=Inter:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inconsolata:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inika:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inconsolata: Inconsolata;
  --font-inika: Inika;
  --font-inter: Inter;

  /* font sizes */
  --font-size-mini: 15px;
  --font-size-6xl: 25px;
  --font-size-21xl: 40px;

  /* Colors */
  --color-black: #000;
  --color-white: #fff;
  --color-darkslateblue: #1b1c48;

  /* border radiuses */
  --br-6xl: 25px;
}
