.weatherForecast {
  position: absolute;
  top: 41px;
  left: 40px;
  font-size: var(--font-size-21xl);
  font-weight: 600;
  font-family: var(--font-inter);
  backdrop-filter: blur(4px);
  -webkit-text-stroke: 1px #000;
}
.iphone14ProMax1Child {
  position: absolute;
  top: 145px;
  left: 40px;
  border-radius: var(--br-6xl);
  background-color: var(--color-darkslateblue);
  width: 356px;
  height: 307px;
}
.sat3Aug,
.today,
.wallStreet2125455 {
  position: absolute;
  font-family: var(--font-inconsolata);
}
.wallStreet2125455 {
  top: 400px;
  left: 105px;
  font-size: 20px;
}
.sat3Aug,
.today {
  top: 179px;
  left: 218px;
}
.today {
  top: 167px;
  left: 55px;
  font-size: var(--font-size-21xl);
}
.iphone14ProMax1Item {
  position: absolute;
  top: 251px;
  left: 58px;
  background-color: var(--color-darkslateblue);
  width: 157px;
  height: 95px;
  opacity: 0.44;
}
.c {
  font-family: var(--font-inconsolata);
}
.oC {
  position: absolute;
  top: 268px;
  left: 50px;
  font-size: 58px;
}
.iphone14ProMax1Inner {
  position: absolute;
  top: 209px;
  left: 202px;
  width: 180px;
  height: 137px;
}
.cloudsIcon,
.streetViewIcon {
  position: absolute;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.cloudsIcon {
  height: 18.88%;
  width: 41.86%;
  top: 22.42%;
  right: 11.16%;
  bottom: 58.69%;
  left: 46.98%;
  object-fit: cover;
}
.streetViewIcon {
  height: 3.43%;
  width: 6.05%;
  top: 42.27%;
  right: 78.6%;
  bottom: 54.29%;
  left: 15.35%;
  animation:revolve 2s infinite
}
@keyframes revolve{
  0%{
      transform:rotate(90deg);
      margin-left: 20%;
  }
}

.streetViewIcon:hover {
 translate:calc(25.5%);
}
.rectangleDiv {
  position: absolute;
  top: 542px;
  left: 24px;
  border-radius: var(--br-6xl);
  background-color: var(--color-darkslateblue);
  border: 1px solid var(--color-black);
  box-sizing: border-box;
  width: 111px;
  height: 134px;
}
.div {
  position: absolute;
  top: 635px;
  left: 56px;
}
.iphone14ProMax1Child1 {
  position: absolute;
  top: 542px;
  left: 152px;
  border-radius: var(--br-6xl);
  background-color: var(--color-darkslateblue);
  border: 1px solid var(--color-black);
  box-sizing: border-box;
  width: 120px;
  height: 134px;
}
.image1Icon {
  position: absolute;
  top: 629px;
  left: 218px;
  width: 36px;
  height: 37px;
  object-fit: cover;
}
.am,
.div1 {
  position: absolute;
  top: 634px;
  left: 180px;
}
.am {
  top: 610px;
  font-size: var(--font-size-mini);
}
.iphone14ProMax1Child2 {
  position: absolute;
  top: 542px;
  left: 292px;
  border-radius: var(--br-6xl);
  background-color: var(--color-darkslateblue);
  border: 1px solid var(--color-black);
  box-sizing: border-box;
  width: 120px;
  height: 134px;
}
.am2,
.div2 {
  position: absolute;
  top: 635px;
  left: 325px;
}
.am2 {
  top: 610px;
  left: 310px;
  font-size: var(--font-size-mini);
}
.image2Icon {
  position: absolute;
  top: 625px;
  left: 366px;
  width: 36px;
  height: 35px;
  object-fit: cover;
}
.image18Icon {
  position: absolute;
  top: 630px;
  left: 90px;
  width: 36px;
  height: 35px;
  object-fit: cover;
}
.today1 {
  position: absolute;
  top: 500px;
  left: 35px;
  font-family: var(--font-inconsolata);
}
.windIcon {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.dayRain,
.dayWind {
  top: 550px;
  height: 56px;
}
.dayWind {
  position: absolute;
  left: 66px;
  width: 56px;
}
.dayRain {
  left: 202px;
  width: 63px;
}
.am4,
.dayRain,
.dayStorm {
  position: absolute;
}
.dayStorm {
  top: 550px;
  left: 344px;
  width: 58px;
  height: 56px;
}
.am4 {
  top: 609px;
  left: 51px;
  font-size: var(--font-size-mini);
}
.iphone14ProMax1 {
  position: relative;
  border-radius: var(--br-6xl);
  background-color: #11103a;
  border: 1px solid var(--color-black);
  box-sizing: border-box;
  width: 100%;
  height: 932px;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-6xl);
  color: var(--color-white);
  font-family: var(--font-inika);
}





.omkar{
  position: absolute;
  color:rgb(242, 34, 2);
  /* font-size:larger; */
  margin-top: 50%;
  margin-left: 40.16%;
}






